.boxclass {
    font-weight: bold;
    margin-top: 1.25rem;
    font-family: 'Open Sans', sans-serif;
    margin-bottom: 0.3rem;
  }
  .email-addres-box {
    border-radius: 12px;
    height: 2rem;
    width: 100%;
    border: 2px solid rgb(252, 248, 248);
    background-color: rgb(218, 218, 218);
  }
  .password-box {
    border-radius: 12px;
    height: 2rem;
    width: 100%;
    border: 2px solid rgb(252, 248, 248);
    background-color: rgb(218, 218, 218);
  }
  
  .boxclass {
    padding-left: 0.5rem;
  }
  
  /*Responsive*/
  
  .boxclass .email-addres-box .password-box {
    display: grid;
    grid-template-columns: 100px 100px 100px;
    grid-template-rows: 50px 50px;
  }